optgroup[label] {
  color: rgb(237, 237, 237);
}

.color-cell {
  height: 10px;
  width: 20px;
  min-width: 20px;
  max-width: 20px;
  border: 1px solid #000;
  padding: 10px;
}

.color-cell.selected {
  border: 1.5px solid white;
}

.fixed-dropdown .dropdown-menu {
  min-width: 230px;
  max-width: 230px;
}

#colorPickerBtn {
  background-color: transparent;
  height: fit-content;
  width: fit-content;
  border: 0;
}